<template>
  <div id="container" :style="{width:mapWidth,height:mapHeight}" class="txMap">
  </div>
</template>

<script >
import TXMap from "@/utils/TXMap";
export default {
  props:{
    mapWidth:String,
    mapHeight:String
  },
  data(){
    return{
      map:null,
      zoom:10
    }
  },
  mounted(){
    TXMap.init().then((TXMap) => {
      this.TXMap = TXMap;
      this.map = new TXMap.Map(document.getElementById("container"), {
        //
        center: new TXMap.LatLng(30.228232, 120.051238), // 
        zoom: this.zoom, //
        draggable: true, //
        scrollwheel: true, //
        disableDoubleClickZoom: true, //
        disableDefaultUI: false, //
      });
    });
  },
  methods:{
    loadMap() {
      new this.TXMap.Circle({
        //
        map: this.map, //
        center: new this.TXMap.LatLng(30.228232, 120.051238), //
        radius: 500, //
        strokeColor: "#fff", //
        fillColor: new this.TXMap.Color(255, 255, 255, 0.3), //
        strokeWidth: 1, //
      });
      data.forEach((x) => {
        //,data->
        new this.TXMap.Polygon({
          map: this.map,
          path: new this.TXMap.LatLng(x.dots), //dots->
          fillColor: new this.TXMap.Color.fromHex("#ccc", 0.5), //
          strokeWeight: 0, //
        });
      });
      let cssC = {
        //
        backgroundColor: "#ccc",
        fontSize: "20px",
        lineHeight: "20px",
        borderRadius: "4px",
        color: "#000",
        border: 0,
      };
      let label = new this.TXMap.Label({
        //(marker)
        map: this.map,
        zIndex: 12,
        content: "123456", //
        offset: new this.TXMap.Size(-10, -10), //()
        position: new this.TXMap.LatLng(30.228232, 120.051238), //
      });
      label.setStyle(cssC);
      data.forEach((x) => {
        //
        new this.TXMap.Polyline({
          map: this.map,
          path: new this.TXMap.LatLng(x.dots),
          strokeWeight: 3, //  1
          StrokeDashStyle: "dash", //
          strokeColor: "#00f", // 
        });
      });
    }
  }
}
</script>

<style>

</style>



